<template>
    <div class="home">
        <!-- header -->
        <Vheader></Vheader>
        <!-- article -->
        <div class="computedData">
            <div class="contentMain">
                <div class="computedDataMain">
                    <h2>{{NearlyMonth}}</h2>
                    <div class="echart" id="computedEchart"></div>
                </div>
            </div>
        </div>
        <!-- footer -->
        <Vfooter></Vfooter>
    </div>
</template>
<script>
import Vheader from '../common/header.vue' //引入头部
import Vfooter from '../common/footer.vue' //引入底部 
import '../../assets/style/index.css'      //主样式
import '../../assets/font/iconfont.css'    //字体图标
import contentVue from '../personageNews/content.vue';
export default {
    name: "computedData",
    components: {
        Vheader, Vfooter,
    },
    data() {
        return {
            dateList: [],
            valueList: [],
            echartData: [],
            loading: false,
            color: ["#e13719", "#007783", "#9eb407", "#f49a0d"],
            NearlyMonth: ''
        }
    },
    watch: {
        "$route.fullPath": {
            handler() {
                this.toggleLang();
                this.getechartData();
            }
        }
    },
    mounted() {
        this.toggleLang();
        this.getechartData();
    },
    methods: {
        drawEchar() {
            // 基于准备好的dom，初始化echarts实例
            const _this = this;
            const chartBox = document.getElementById('computedEchart');
            const size = chartBox.clientWidth;
            const width = size + "px";
            const height = size * 0.5 + "px";
            let myChart = this.$echarts.init(chartBox, null, {
                width,
                height,
            });
            // 绘制图表
            myChart.setOption(
                {
                    visualMap: [{
                        show: false,
                        type: 'continuous',
                        seriesIndex: 1,
                        min: 0,
                        max: 400
                    }],
                    title: [{
                        left: 'center'
                    }],
                    tooltip: {
                        trigger: 'axis',
                        formatter: '{b} <br /> 当日访问量 : {c0}<br />'
                    },
                    xAxis: [{
                        data: this.dateList,
                        axisLabel: {
                            show: true
                        },

                    }],
                    yAxis: [{
                        splitLine: { show: false }
                    }],
                    grid: [{
                        bottom: '30%'
                    }],
                    series: [{
                        type: 'line',
                        showSymbol: false,
                        data: this.valueList,
                    }]                }
            );
        },
        getechartData() {
            const _this = this;
            let html = "";
            this.echartData = [];
            this.loading = true;
            this.commonMethods.emitAjax({
                path: "/api/front/countInfo",
                success(response) {
                    _this.loading = false;
                    _this.dateList = [];
                    _this.valueList = [];
                    var sv = [];
                    for (var i in response) {
                        sv.push([i, response[i]])
                    };
                    sv.sort(sortNumber);//排序
                    function sortNumber(a, b) {
                        var dt_a = a[0], dt_b = b[0];
                        var time_a = parseInt(new Date(dt_a).getTime());
                        var time_b = parseInt(new Date(dt_b).getTime());
                        return time_a - time_b;
                    }
                    for (var i = 0; i < sv.length; i++) {
                        _this.dateList.push(sv[i][0]);
                        _this.valueList.push(sv[i][1]);
                    }
                    _this.drawEchar();
                },
                error() {
                    _this.loading = false;
                }
            })
        },
        toggleLang() {
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
            this.NearlyMonth = _this.lang.NearlyMonth;
        }
    }

};
</script>


