<template>
    <!-- 门户首页 -->
    <header class="header">
        <!-- <div class="login_success" v-else @mouseenter="showDownMenu()" @mouseleave="hideDownMenu()">
            <span class="name">{{lang.welcome_you}}&nbsp;!&emsp;{{userInfo}}
                <Icon type="arrow-down-b"></Icon>
            </span>
            <div class="DropdownMenu" v-if="MenuShow">
                <ul class="clearfix">
                    <li>
                        <a href="http://10.203.8.48:8090/baseinfo.php">{{lang.manage_home}}</a>
                    </li>
                    <li>
                        <a href="http://10.203.8.48:8090/casLogin.php?logout=1">{{lang.logout}}</a>
                    </li>
                </ul>
            </div>
        </div>-->
        <div class="header-main">
            <h3 class="header-title">
                {{lang.homepage}}
                <div class="loginBtn" @click="login" v-if="userInfo==''"></div>
            </h3>
            <div class="toggleLang">
                <div class="langType" :class="{active:langType=='cn'||langType==null}">
                    <a @click="toggleLang('')">中文</a>
                </div>
                <div class="langType"  :class="{active:langType=='en'}">
                    <a @click="toggleLang('en')">English</a>
                </div>
            </div>
            <div class="search clearfix">
                <input type="text" v-model="keyword" @keydown.enter="searchList()" class="search-input fl">
                <button class="search-btn fr" @click="searchList()">搜索</button>
            </div>
        </div>
    </header>
</template>
<script>
import SearchKeyword from '../../assets/js/searchKeyword' //引入头部
export default {
    data() {
        return {
            mbflg: true, //手机端导航
            fixed: false,
            keyword: '',
            page: 0,
            MenuShow: false,
            loginstatus: '1',
            lang: {},
            langType: "cn",
        }
    },
    props: {
        personalDetail: {
            type: Boolean
        }
    },
    computed: {
        //高级搜索侧边参数获取
        getsearchAdvancedParams() {
            return this.$store.state.searchAdvancedParams;
        },
        gethotListBACKUP() {  //获取热门备份数据 ，为了在seaech本页面 无关键字
            return this.$store.state.hotListBACKUP;
        },
        userInfo() {
            return this.$store.state.userInfo;
        }
    },
    mounted: function () {
        var keyword = this.$route.query.keyword;  //远程获取路由参数
        if (keyword) {
            this.keyword = keyword;//设置远程关键字  
        } else {
            this.keyword = this.$store.state.keyword;//设置本地关键字  
        };
        this.getSearchKeyword(); //全局函数--去除搜索关键字
        this.templateToggleLang();
    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.templateToggleLang();
            }
        }
    },
    methods: {
        getSearchKeyword: function () {
            SearchKeyword.$on("keyword", (message) => {   //这里最好用箭头函数，不然this指向有问题
                this.$store.state.keyword = ''
                this.keyword = ''
            })
        },
        login: function () {
            window.location.href = "http://10.203.8.48:8090/casLogin.php";
        },
        showDownMenu: function () {
            this.MenuShow = true;
        },
        hideDownMenu: function () {
            this.MenuShow = false;
        },
        mbnav: function () {
            this.shownav = !this.shownav;
            this.fixed = !this.fixed;
        },
        searchList: function () {
            var keyword = this.keyword, size = 12;
            if (keyword == '' || keyword == null) {
                this.$store.commit('clearkeySerach'); //清空关键字
                this.$emit('cleartAdvancedParams', 'cleartAdvancedParams'); //清空关键字页数 
                if (this.$store.state.hotListBACKUP.length > 0) { //this.$store.state.hotListBACKUP 备份数据 
                    this.$store.state.hotList = this.$store.state.hotListBACKUP  //当跳到搜索页面的时候再无关键字搜索那么备份赋值 
                };
                if (this.langType != "cn") {
                    this.$router.push({ name: this.langType + 'search' });
                } else {
                    this.$router.push({ name: 'search' });
                }
            } else {
                this.$store.commit('setKeyword', keyword); //设置缓存关键字  
                this.$emit('cleartAdvancedParams', 'cleartAdvancedParams'); //清空关键字页数 
                this.$store.state.searchAdvancedList = []; //先清空本地数据--（出现图片加载延迟）
                this.$store.commit('clearhotList'); //清空热点数据
                this.$store.commit('clearhotListBACKUP');
                var params = {
                    q: keyword, //关键字
                    size: size,//条数
                    page: this.page //页面数默认0
                };
                this.$store.commit('setclickMoresize', 12) //重新搜索关键字  初始化默认12条数
                this.$store.commit("search_More", params);
                if (this.langType != "cn") {
                    this.$router.push({ name: this.langType + 'search' });
                } else {
                    this.$router.push({ name: 'search' });
                }
            }
        },
        navTip() {
            this.$Modal.warning({
                title: '',
                content: '暂未开放！'
            });
        },
        toggleLang(lang) {
            //切换语言 
            const _this = this;
            let url = this.$route.fullPath;
            let isCn = true;
            //循环各语言
            for (let index = 0; index < this.config.langTypeArray.length; index++) {
                const langtype = this.config.langTypeArray[index];
                //如果当前语言是langtype，排除中文 且点击的是别的语言则跳转
                if (url.indexOf("/" + langtype + "/") >= 0) {
                    isCn = false;
                    if (lang != langtype) {
                        url = url.replace("/" + langtype, lang ? "/" + lang : "");
                    }
                    break;
                }
            }
            //中文状态切换别的语言
            if (isCn && lang) {
                url = "/" + lang + url;
            };
            this.keyword = ''; //切换清空搜索款关键字
            this.$emit('cleartAdvancedParams', 'cleartAdvancedParams'); //切换先清空关键字页数 
            this.$store.state.searchAdvancedList = []; //切换先清空本地（高级搜索）数据
            this.$store.commit('clearhotList'); //切换先清空热点数据
            this.$store.commit('clearhotListBACKUP');//切换先清空热点备份数据
            if (url == '/en/personageNewList') {
                this.$router.push('/en');  //英文人物新闻
            } else {
                this.$router.push(url);
            };
        },
        templateToggleLang() {
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.langType = langType;
                _this.lang = _this.langOption[langType];
            })
        }
        
    }
}

</script>


