<template>
	<footer class="footerNav">
        <ul class="clearfix nav-menu">
            <li  :class="['home',{active:($route.path != '/computedData' && $route.path !=('/' + langType + '/computedData')&&$route.path != '/personageNewList')}]">
                <router-link :to="langType != 'cn' ? '/'+langType+'/hot' : '/hot'">
                    <div class="navIcon"></div>
                    <div class="navLink">{{lang.home}}</div>
                </router-link>
            </li>
            <li :class="['news',{active:$route.path == '/personageNewList'}]" v-if="langType=='cn'">
                <router-link to="/personageNewList">
                    <div class="navIcon"></div>
                    <div class="navLink">{{lang.news}}</div>
                </router-link>
            </li>
            <li :class="['computedData',{active:($route.path == '/computedData' ||$route.path==('/' + langType + '/computedData'))}]">
                <router-link :to="langType != 'cn' ? ('/' + langType + '/computedData') : '/computedData'">
                    <div class="navIcon"></div>
                    <div class="navLink">{{lang.platform_statistics}}</div>
                </router-link>
            </li>
            <li class="myzju">
                <a href="http://my.zju.edu.cn" target="_blank">
                    <div class="navIcon"></div>
                    <div class="navLink">MYZJU</div>
                </a>
            </li>
        </ul>
	</footer>
</template>
<script>
export default {
	props: {
		personalDetail: {
			type: Boolean,
			default: true,
		}
    },
    data(){
        return {
            lang:{},
            langType:"cn",
            navArray: [],   // 导航
        }
    },
    watch:{
        "$route.fullPath":{
            handler(){
                this.toggleLang();
            }
        }
    },
    methods:{
        toggleLang(){
            const _this = this;
            this.commonMethods.selectLangType(function(langType){
                _this.langType = langType;
                _this.lang = _this.langOption[langType];
            })
        },
    },
    mounted(){
        this.toggleLang();
    }
}
</script>


